<template>
  <div class="indexFooter">
    <div
      v-for="(item,index) in sectionList"
      :key="index"
      :class="sectionClass(item)"
      :style="[{top:item.top + 'px'}, {left: (item.click ? (item.left - 35 + 'px') : (item.left + 'px')) }]"
      @click="clickSection(item)"
    >
      <div class="section-title"> {{item.sectionName}} </div>
    </div>
  </div>
</template>

<script>
import { getToken, getSectionList } from '../../../utils/api/api.js'
export default {
  name: 'index-footer',
  data() {
    return {
      sectionList: [
        {
          sectionName: 'T1',
          left: 145,
          top: 31,
          click: true
        },
        {
          sectionName: 'T2',
          left: 273,
          top: 5,
          click: false
        },
        {
          sectionName: 'T3',
          left: 398,
          top: -10,
          click: false
        },
        {
          sectionName: 'T4',
          left: 523,
          top: -15,
          click: false
        },
        {
          sectionName: 'T5',
          left: 648,
          top: -20,
          click: false
        },
        {
          sectionName: 'T6',
          left: 773,
          top: -15,
          click: false
        },
        {
          sectionName: 'T7',
          left: 898,
          top: -10,
          click: false
        },
        {
          sectionName: 'T8',
          left: 1023,
          top: 5,
          click: false
        },
        {
          sectionName: 'T9',
          left: 1148,
          top: 31,
          click: false
        }
      ]
    }
  },
  computed: {
    sectionClass: function () {
      return function (item) {
        if (item.click) { return 'section-one section-one-sel' } else { return 'section-one' }
      }
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.initToken()
    })
  },
  methods: {
    clickSection(item) {
      this.sectionList.forEach(obj => {
        obj.click = false
      })

      item.click = true
      this.changeSelectSection(item)
    },
    initToken() {
      getToken().then(res => {
        if (res.code === 200) {
          CONFIG_SERVICE.tempToken = res.token
          this.initPages()
        } else {
          this.$Message.warning({
            content: res.msg,
            background: true,
            closable: true
          })
        }
      }).catch(erro => {
        this.$Message.warning({
          content: erro,
          background: true,
          closable: true
        })
        console.log(erro)
      })
    },
    initPages() {
      getSectionList().then(res => {
        if (res.code === 200) {
          this.sectionList = res.rows.map((item, index) => {
            // 去除字符串空格
            if (item.sectionName && CONFIG.isTrimSectionNameTrim) {
              item.sectionName = item.sectionName.replace(/\s*/g, '')
            }
            const obj = {
              ...this.sectionList[index],
              ...item
            }
            if (this.sectionList[index].click) {
              this.changeSelectSection(obj)
            }
            return obj
          })
        } else {
          this.$Message.warning({
            content: res.msg,
            background: true,
            closable: true
          })
        }
      }).catch(erro => {
        this.$Message.warning({
          content: erro,
          background: true,
          closable: true
        })
        console.log(erro)
      })
    },
    changeSelectSection(item) {
      this.$store.commit('changeSelectSection', item)
    }

  }
}
</script>

<style lang="less" scoped>
.indexFooter {
  width: 100%;
  height: 105px;
  margin-left: 293px;
  position: absolute;
  z-index: 1;
  bottom: 0;
  background: url("../../../assets/img/background/dibubiaoduan.png") no-repeat;
}
.section-one {
  position: absolute;
  z-index: 2;
  height: 64px;
  width: 64px;
  background: url("../../../assets/img/background/biaoduan.png");
  display: flex;
  align-items: center;
  justify-content: center;
  color: #a3aec0;
}
.section-one-sel {
  width: 132px;
  height: 64px;
  color: #58e3fb;
  background: url("../../../assets/img/background/biaoduan_sel.png");
}

.section-title {
  height: 36px;
  // font-size: 16px;
  font-size: 14px;
  font-family: MicrosoftYaHei-Bold, MicrosoftYaHei;
  font-weight: bold;
  line-height: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  cursor: pointer;
}
</style>
