<template>
  <div class="weatherPop">
      <div 
        class="weather-one"
        v-for="(item,index) in weatherList"
        :key="index"
        :style="{border: item.select ? '1px dashed #5BE3FB' : '1px dashed gray'  }"
        @click="changeWeather(item.name)"
      >
        <img :src="item.select ? item.imgSelectUrl : item.imgUrl" height="28" width="32" />
      </div>
  </div>
</template>

<script>
export default {
    name: 'weather-pop',
    data(){
        return{
            currentWeather: 'day',
            weatherList: [
                {
                    name:'rain',
                    imgUrl: require('../../../assets/img/background/yu.png'),
                    imgSelectUrl: require("../../../assets/img/background/yu_sel.png"),
                    select: false,
                },
                {
                    name:'snow',
                    imgUrl: require('../../../assets/img/background/xue.png'),
                    imgSelectUrl: require("../../../assets/img/background/xue_sel.png"),
                    select: false,
                },
                {
                    name:'day',
                    imgUrl: require('../../../assets/img/background/qing.png'),
                    imgSelectUrl: require("../../../assets/img/background/qing_sel.png"),
                    select: false,
                }
            ]
        }
    },
    // computed:{
    //     currentWeather(){
    //         return this.$store.getters.getCurrentWeather
    //     }
    // },
    watch:{
        currentWeather:{
            handler(newVal, oldVal){
                this.weatherList.forEach( obj=>{
                    if(obj.name == newVal){
                        obj.select = true;
                        this.changeUeWeather(obj.name);
                    }else{
                        obj.select = false;
                    }
                })
            },
            immediate: true
        }
    },
    methods:{
        changeWeather(val){
            this.currentWeather = val;
        },
        changeUeWeather(val){
            let ueNum;
            switch(val){
                case 'day':
                    ueNum = 0;
                    break;
                case 'rain':
                    ueNum = 2;
                    break;
                case 'snow':
                    ueNum = 3;
                    break;
            }
            ue4("weather", ueNum);
            console.log(ueNum);
        }
    }
}
</script>

<style lang="less" scoped>
.weatherPop{
    width: 100%;
    padding-left: 15px;
    display: flex;
    align-items: center;
}
.weather-one{
    float: left;
    display: flex;
    border: 1px dashed gray;
    margin-right: 15px;
}

</style>