<template>
  <div class="timeInfo">
    <span> 天气 : </span>
    <span class="text-weather"> {{timeInfo.weather}} </span>
    <img
      :src="weatherIcon"
      class="weather-img"
    />
    <span class="text-temperature"> {{ timeInfo.temperature }} ℃ </span>
    <span class="text-date"> {{timeInfo.date}} </span>

    <Button
      type="primary"
      @click="changeFloatPagesVisible"
    >
      <div class="button-content">
        <img src="../../../assets/img/icon/menu.svg" />
        <span>菜单</span>
      </div>
    </Button>

  </div>
</template>

<script>
export default {
  name: 'time-info',
  props: {
    timeInfo: {
      type: Object,
      default: {
        weather: '晴',
        temperature: 18,
        date: '2022年3月28日21:30:41'
      }
    }
  },
  data() {
    return {
      weatherIcon: require('../../../assets/img/icon/day.svg'),
      meneIcon: require('../../../assets/img/icon/menu.svg'),
    }
  },
  computed: {
    floatPagesVisible() {
      return this.$store.getters.getFloatPagesVisible
    }
  },
  methods: {
    changeFloatPagesVisible() {
      this.$store.commit('changeFloatPagesVisible', !this.floatPagesVisible);
    }
  }
}
</script>

