<template>
  <div
    class="projectList"
    @click="handleClick($event)"
  >
    <vue-seamless-scroll
      :data="dataList"
      v-if="dataList.length > 0"
      class="scroll"
      :class-option="classOption"
    >
      <div
        class="project-one"
        v-for="(item,index) in projectList"
        :key="index"
      >
        <img
          :src="item.img"
          :data-dept="item.title"
        />
        <div
          class="project-one-title"
          :data-dept="item.title"
        >
          {{item.title}}
        </div>
      </div>
    </vue-seamless-scroll>
  </div>
</template>

<script>
import { getSectionListExt } from '../../../utils/api/api.js'
export default {
  name: 'project-list',
  data() {
    return {
      dataList: [],
      projectList: [
        {
          img: require('../../../assets/img/background/tukuang.png'),
          title: 'xxx特大桥主桥左幅一'
        },
        {
          img: require('../../../assets/img/background/tukuang.png'),
          title: 'xxx特大桥主桥左幅二'
        },
        {
          img: require('../../../assets/img/background/tukuang.png'),
          title: 'xxx特大桥主桥左幅三'
        },
        {
          img: require('../../../assets/img/background/tukuang.png'),
          title: 'xxx特大桥主桥右幅四'
        }

      ]
    }
  },

  computed: {
    classOption() {
      return {
        step: 0.3, // 数值越大速度滚动越快
        limitMoveNum: 1, // 开始无缝滚动的数据量 this.dataList.length
        hoverStop: true, // 是否开启鼠标悬停stop
        direction: 1, // 0向下 1向上 2向左 3向右
        openWatch: true, // 开启数据实时监控刷新dom
        singleHeight: 0, // 单步运动停止的高度(默认值0是无缝不停止的滚动) direction => 0/1
        singleWidth: 0, // 单步运动停止的宽度(默认值0是无缝不停止的滚动) direction => 2/3
        waitTime: 1000, // 单步运动停止的时间(默认值1000ms)
        autoPlay: true
      }
    },
    selectSection: function () {
      return this.$store.getters.getSelectSection
    }
  },
  watch: {
    selectSection: {

      handler(newVal) {
        if (newVal.sectionId) {
          this.initProjectList(newVal.sectionId)
        }
      },
      immediate: true,
      deep: true // true为进行深度监听,false为不进行深度监听
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.dataList = this.projectList
    })
  },
  methods: {
    initProjectList(sectionId) {
      getSectionListExt(sectionId).then(res => {
        if (res.code === 200) {
          // const sectionArray = res.rows.map((item, index) => {
          //   const obj = {
          //     ...this.sectionList[index],
          //     ...item
          //   }
          //   return obj
          // })
          // this.changeSectionList(sectionArray)
        } else {
          this.$Message.warning({
            content: res.msg,
            background: true,
            closable: true
          })
        }
      }).catch(erro => {
        this.$Message.warning({
          content: erro,
          background: true,
          closable: true
        })
        console.log(erro)
      })
    },
    handleClick(event) {
      const title = event.target.dataset.dept
      const item = this.projectList.find(obj => {
        return obj.title == title
      })
      console.log(item)
      if (!item) return

      this.showProject(item)
    },
    showProject(item) {

    }
  }
}
</script>

<style lang="less" scoped>
.projectList {
  width: 100%;
  height: calc(100% - 70px);
  overflow: hidden;
}

.scroll {
  height: 100%;
  width: 100%;
  overflow: hidden;
  scrollbar-width: none; //兼容火狐
}
.scroll::-webkit-scrollbar {
  display: none; //兼容chrome、safari
}
.project-one {
  width: 100%;
  height: 200px;
  margin-bottom: 20px;
  position: relative;
  cursor: pointer;
}
.project-one-title {
  position: absolute;
  bottom: 20px;
  z-index: 2;
  height: 31px;
  width: 100%;
  padding: 0 20px;
  font-size: 24px;
  font-family: YouSheBiaoTiHei;
  color: #5be3fb;
  line-height: 31px;
}
</style>
