<template>
  <div class="timePop">
        <Slider 
            v-model="currentTime"
            :step="0.1" 
            :min="1"
            :max="24"
            show-tip="never"
            @on-input="changeTime"
            :marks="marks"
        >
        </Slider>
  </div>
</template>

<script>
export default {
    name: 'time-pop',
    data(){
        return{
            currentTime: 9.6,
            marks:{
                5:{
                    style: {
                            color: '#ff0000'
                        },
                        label: this.$createElement('strong', '55°C')
                }
            }
        }
    },
    mounted(){
        this.$nextTick(()=>{
            this.changeTime(this.currentTime);
        })
    },
    computed:{

    },
    methods:{
        changeTime(val){
            console.log(val)
            ue4("time", val);
        }
    }
}
</script>

<style lang="less" scoped>
.timePop{
    width: 150px;
    padding: 0 10px;
}
</style>