<template>
    <div class="layerPop">
        <!-- <CheckboxGroup 
            v-model="layerCheckList"
            @on-change="changeLayers"
        >
            <Checkbox @on-change="changeTiltLayers" label="倾斜"></Checkbox>
            <Checkbox @on-change="changeBimLayers" label="BIM"></Checkbox>
        </CheckboxGroup> -->
        <Checkbox v-model="showTilt"  @on-change="changeTiltLayers">倾斜</Checkbox>
        <Checkbox v-model="showBim"  @on-change="changeBimLayers">BIM</Checkbox>
    </div>
</template>

<script>
export default {
    name: 'layer-pop',
    data(){
        return{
            showTilt: true,
            showBim: true,
        }
    },
    mounted(){
        this.$nextTick(()=>{
            this.changeTiltLayers(true);
            this.changeBimLayers(true);
        })
    },
    methods:{
        changeTiltLayers(val){
            let visible = val ? 'true' : 'false';
            let tiltLayers = CONFIG.tiltLayers;
            tiltLayers.forEach( name => {
                let obj = {
                    layer: name,
                    visibility: visible
                };
                let string = name + ',' + val;
                // let arr = [name, String(val)];
                ue4("setLayerVisible", string);
                console.log(string);
            });
        },
        changeBimLayers(val){
            let visible = val ? 'true' : 'false';
            let bimLayers = CONFIG.bimLayers;
            bimLayers.forEach( name => {
                let obj = {
                    "layer": name,
                    "visibility": visible
                };
                // let string = JSON.stringify(obj);
                let string = name + ',' + val;
                ue4("setLayerVisible", string);
                console.log(string);
            });
           
        }
    }
}
</script>

<style lang="less" scoped> 
.layerPop{
    
    font-size: 18px;
    font-family: MicrosoftYaHei;
    color: #5BE3FB;
    letter-spacing: 1px;
    padding-left: 10px;

    /deep/ .ivu-checkbox-inner{
        background: none;
        border: 1px solid #5BE3FB;
    }
    /deep/ .ivu-checkbox-checked .ivu-checkbox-inner{
        background: none;
    }
    /deep/ .ivu-checkbox-checked .ivu-checkbox-inner:after{
        border: 2px solid #5BE3FB;
        border-top: 0; 
        border-left: 0; 
        transform: rotate(45deg) scale(1);
        transition: all .2s ease-in-out;
    }
    // /deep/ .ivu-checkbox-inner:after{
    //     border: 2px solid #5BE3FB;
    // }
}
</style>