import request from './request.js'

const loginBaseUrl = CONFIG_SERVICE.loginBaseUrl
const baseUrl = CONFIG_SERVICE.baseUrl

export function getToken() {
  const url = loginBaseUrl + '/login'
  const params = {
    username: CONFIG_SERVICE.login.username,
    password: CONFIG_SERVICE.login.password
  }
  const options = {
    headers: {
      'content-type': 'application/json;charset=UTF-8',
      Authorization: ''
    }
  }
  return request.post(url, params, options)
}
export function getSectionList() {
  const url = baseUrl + '/section/list'
  return request.get(url, {})
}

export function getSectionListExt(sectionId) {
  const url = baseUrl + '/section/listext'
  return request.get(url, {
    sectionId: sectionId
  })
}
