import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    floatPagesVisible: true, // 控制， 底部栏，右侧区域显示
    selectSection: {}, //当前选中的标段
    // currentWeather: 'day',
    // currentTime: 9.6,
    initVisible: true,
    screenSize: 'normal'// 'big' | 'normal',
  },
  getters: {
    getFloatPagesVisible(state) {
      return state.floatPagesVisible
    },
    getCurrentWeather(state){
      return state.currentWeather;
    },
    // getCurrentTime(state){
    //   return state.currentTime;
    // },
    getSelectSection(state) {
      return state.selectSection
    },
    getScreenSize(state) {
      return state.screenSize
    },
    getInitVisible(state) {
      return state.initVisible
    }
  },
  mutations: {
    changeScreenSize(state, sizeString) {
      state.screenSize = sizeString
    },
    changeInitVisible(state, visible) {
      state.initVisible = visible
    },
    changeFloatPagesVisible(state, visible) {
      state.floatPagesVisible = visible
    },
    changeSelectSection(state, item) {
      state.selectSection = item
    },
    // changeWeather(state, val){
    //   state.currentWeather = val;
    // },
    // changeTime(state, val){
    //   state.currentTime = val;
    // },
  },
  actions: {
  },
  modules: {
  }
})
